import React from 'react';
import { connect } from 'react-redux'
import { setDrinkNr, findDrinkByNr } from '../drinks/drinks-actions'
import './digipad.scss'

const stateToProps = state => {
    const { nr } = state.drinks.drinksList
    const drinksLen = state.drinks.drinks.length

    return {
        nr,
        drinksLen
    };
};

function dispatchToProps(dispatch) {
    return {
        setDrinkNr: nr => dispatch(setDrinkNr(nr)),
        findDrinkByNr: nr => dispatch(findDrinkByNr(nr))
    };
}

const rows = [
    [1, 2, 3],
    [4, 5, 6],
    [7, 8, 9]
]

const rowBtm = [-1, 0]

function DigiPad({ nr, drinksLen, setDrinkNr, findDrinkByNr }) {

    const text = nr.length ? nr : 'numer drinka'
    const textClass = nr.length ? ' ' : ' digipad-txt-placeholder'

    const btnsInactiveCssClass = ' keyboard-btn-inactive'
    const btnsActive = !!nr.length
    const btnInactiveCssClass = btnsActive ? '' : btnsInactiveCssClass
    const showBtnInactiveCssClass = btnsActive && drinksLen > 1 ? '' : btnsInactiveCssClass

    return (<div className='digipad'>

        <div className='digipad-row digipad-row-txt'>
            <span
                onClick={() => btnsActive ? setDrinkNr('') : false}
                key={'kbrdcls'}
                className={'keyboard-btn' + btnInactiveCssClass}>
                czyÅÄ
                </span>
            <span className={'digipad-txt' + textClass}>{text}</span>

            <span
                onClick={() => btnsActive ? setDrinkNr(nr.slice(0, -1)) : false}
                className={'keyboard-btn' + btnInactiveCssClass}>
                {'â«'}
            </span>
        </div>

        {rows.map(row => {
            return (<div className='digipad-row'>
                {row.map(s => {
                    return <span
                        onClick={() => setDrinkNr(nr + s)}
                        key={'kbrd' + s}
                        className='keyboard-btn'>
                        {s}
                    </span>
                })}
            </div>)
        })}

        <div className='digipad-row digipad-row-btm'>

            {rowBtm.map(s => {
                return <span
                    onClick={() => setDrinkNr(nr + s)}
                    key={'kbrd' + s}
                    className='keyboard-btn'>
                    {s}
                </span>
            })}

            <span className={'keyboard-btn' + showBtnInactiveCssClass}
                onClick={() => (btnsActive && drinksLen > 1) ? findDrinkByNr(nr) : false}>
                pokaÅ¼
            </span>
        </div>

    </div>)
}

export default connect(stateToProps, dispatchToProps)(DigiPad)
